<style scoped>
#animation{
    height: 200vh;
}
#sticky_box{
    position: sticky;
    top: 12vh;
}
#pointer{
  position: sticky;
  top: 50vh;
  width: 10%;
  z-index: 10;
}
a{
  text-decoration: none;
