    <div class="chapter">
        <div class="pointer to_left">
            <p class="router_title"></p>
            <router-link to="/israel1"  @mouseover.native="hovereffect" @mouseout.native="hoverouteffect">&lt;</router-link>
        </div>
        <div class="pointer to_right">
            <p class="router_title"></p>
            <router-link to="/pal1"  @mouseover.native="hovereffect" @mouseout.native="hoverouteffect">&gt;</router-link>
        </div>
        <div class="text_box">
            <h1></h1>
            <p>191
            <br>
            <br>
            </p>
        </div>
        <div class="images" @mouseover="imageDisc" @mouseout="imageDiscOut">
            <img id="img1" src="../assets/chap1_1.jpg" />
            <img id="img2" src="../assets/chap1_2.jpg" />
            <img id="img3" src="../assets/chap1_3.jpg" />
            <img id="img4" src="../assets/chap1_4.jpg" />
            <img id="img5" src="../assets/chap1_5.jpg" />
            <img id="img6" src="../assets/chap1_6.jpg" />
        </div>
        <div>
            <div class="discs" id="disc1"><p> (1940)</p></div>
            <div class="discs" id="disc2"><p>  (1930)</p></div>
            <div class="discs" id="disc3"><p> (1943)</p></div>
            <div class="discs" id="disc4"><p> (1910)</p></div>
            <div class="discs" id="disc5"><p> (1910)</p></div>
            <div class="discs" id="disc6"><p> (1920)</p></div>
        </div>
    </div>
</template>
<style scoped>
.chapter{
    position: relative;
    height: 100vh;
    justify-content: center;
    align-content: center;
    text-align: center;
    display: block;
    margin: 0;
    background-color: #3a3a3a;
}
.pointer{
    position: absolute;
    top: 45vh;
    width: 10%;
    z-index: 10;
}
.pointer.to_right{
    right: 0vw;
}
.pointer.to_left{
    left: 0vw;
}
.pointer a{
    text-decoration: none;
    font-size: 500%;
    height: 100%;
}
.pointer.to_left a{
    color: #0071ba;
}
.pointer.to_right a{
    color: #0e8136;
}
.router_title{
    margin: 0;
    font-size: 20px;
    opacity: 0;
    transform: translateY(20px);
    transition: all .5s ease-in-out;
}
.router_show{
    opacity: 1;
    transform: translateY(0px);
}
.text_box{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50vh;
    left: 50vw;
    width: 25%;
    /* height: 80vh; */
    color: #e1e1e1;
    z-index: 1;
}
.text_box h1{
    font-size: 80px;
    border-bottom: 3px solid #e1e1e1;
    margin-bottom: 25px;
}
img{
    position: absolute;
    z-index: 2;
}
#img1, #disc1{
    top: 97%;
    left: 10%;
    width: 28%;
    transform: translateY(-100%);
}
#img2, #disc2{
    top: 5%;
    left: 17%;
    width: 21%;
}
#img3, #disc3{
    top: 65%;
    left: 9%;
    width: 22%;
    transform: translateY(-100%);
}
#img4, #disc4{
    top: 95%;
    right: 13%;
    width: 23%;
    transform: translateY(-100%);
}
#img5, #disc5{
    top: 5%;
    right: 15%;
    width: 22%;
}
#img6, #disc6{
    top: 63%;
    right: 7%;
    width: 15%;
    transform: translateY(-100%);
}
.discs{
    position: absolute;
    color: #e1e1e1;
    background-color: rgba(58,58,58,0.5);
    z-index: 3;    
    height: 50px;
    opacity: 0;
    transition: opacity .3s ease-in-out;
}
</style>
<script>
export default{
    methods:{
        imageDisc(event){
            var elID = parseInt(event.target.id.slice(3));
            var target = document.getElementById("disc"+elID);
            target.style.opacity = 1;
        },
        imageDiscOut(event){
            var elID = parseInt(event.target.id.slice(3));
            var target = document.getElementById("disc"+elID);
            target.style.opacity = 0;
        },
        hovereffect(event){
            var trg = event.target.previousSibling;
            trg.classList.add("router_show");
            // console.log(trg);
        },
        hoverouteffect(event){
            var trg = event.target.previousSibling;
            trg.classList.remove("router_show");
            // console.log(trg);
        }
    }
}
</script>